export default {
    data() {
        return {
            modalName: null,
            listTouchStart: 0,
            listTouchDirection: null,
            booklist: ''
            // booklist: [
            //     {
            //         title: '新概念51单片机C语言教程',
            //         author: ['郭天祥'],
            //         image: 'https://img1.doubanio.com/view/subject/m/public/s6457519.jpg',
            //         pubilsher: '电子工业出版社',
            //         price: '69.00',
            //         pubdate: '2019-1'
            //     },
            //     {
            //         title: '新概念51单片机C语言教程',
            //         author: ['郭天祥'],
            //         image: 'https://img1.doubanio.com/view/subject/m/public/s6457519.jpg',
            //         pubilsher: '电子工业出版社',
            //         price: '69.00',
            //         pubdate: '2019-1'
            //     }
            // ]
        };
    },
    onLoad() {
        console.log('List Load');
        // this.booklist = uni.getStorageSync('booklist');
        // console.log(this.BookList);
        // console.log('get cahce success');
    },
    onShow() {
        console.log('List Show');
        this.booklist = uni.getStorageSync('booklist');
        console.log(this.booklist);
        console.log('get cahce success');
    },
    onHide() {
        // this.BookList = this.booklist;
        // console.log(this.BookList);
        console.log('List Hide');
    },
    methods: {
        removeBook(e) {
            var index = e.currentTarget.dataset.index;
            console.log(e.currentTarget.dataset.index);
            this.booklist.splice(index, 1);
            uni.setStorageSync('booklist', this.booklist);
        },
        // ListTouch触摸开始
        ListTouchStart(e) {
            this.listTouchStart = e.touches[0].pageX;
        },

        // ListTouch计算方向
        ListTouchMove(e) {
            this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left';
        },

        // ListTouch计算滚动
        ListTouchEnd(e) {
            if (this.listTouchDirection == 'left') {
                this.modalName = e.currentTarget.dataset.target;
            } else {
                this.modalName = null;
            }
            this.listTouchDirection = null;
        }
    }
};
